/* Base Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Segoe UI', sans-serif;
  background-color: #f9f9f9;
  color: #333;
  scroll-behavior: smooth;
}

/* ===== Header ===== */
.header {
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  position: sticky;
  top: 0;
  z-index: 100;
  width: 100%;
}

/* ===== Nav Container ===== */
.nav-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

/* ===== Logo Styling ===== */
.logo {
  display: flex;
  align-items: center;
}

.logo img {
  height: 80px;
  width: auto;
  object-fit: contain;
}

/* Optional: Responsive Adjustment */
@media (max-width: 768px) {
  .logo img {
    height: 70px;
  }

  .nav-container {
    padding: 1rem;
  }
}


/* Navigation */
.nav-menu {
  display: flex;
  gap: 1.5rem;
  list-style: none;
  margin-right: 200px;
  
}
.nav-item a {
  text-decoration: none;
  font-weight: 500;
  color: #636363;
  transition: color 0.3s ease;
}
.nav-item a:hover {
  color: #040404;
}

/* Add this for selected item */
.nav-item a.active-link {
  color: #070707; /* Highlight color */
  
}
/* Mobile menu toggle (hamburger) */
.mobile-menu-toggle {
  display: none;
  flex-direction: column;
  gap: 5px;
  cursor: pointer;
}

.mobile-menu-toggle span {
  width: 25px;
  height: 3px;
  background-color: #333;
  border-radius: 2px;
}


@media (max-width: 768px) {
  .mobile-menu-toggle {
    display: flex;
  }

  .nav-menu {
    position: absolute;
    top: 70px;
    /* right: 0; */
    background: #fff;
    flex-direction: column;
    width: 100%;
    display: none;
    text-align: center;
    padding: 1rem 0;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  }

  .nav-menu.active {
    display: flex;
  }

  .nav-item {
    margin: 1rem 0;
  }
}
/* ===== Hero Section ===== */
.hero {
  position: relative;
  height: 100vh;
  display: flex;
  align-items:flex-start;
  justify-content: left;
  text-align: left;
  color: #fff;
  overflow: hidden;
  /* padding: 2rem; */
  padding-top: 100px; 
}

/* Background Slideshow */
.hero::before {
  content: "";
  position: absolute;
  top: 0; left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  animation: slideshow 15s infinite;
  z-index: -1;
  /* opacity: 0.1; */
}

/* Floating Emojis */
.floating-element {
  position: absolute;
  font-size: 2.5rem;
  animation: float 6s ease-in-out infinite;
}
.floating-element:nth-child(1) { top: 10%; left: 10%; }
.floating-element:nth-child(2) { top: 30%; right: 10%; }
.floating-element:nth-child(3) { bottom: 15%; left: 25%; }

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-15px); }
}

/* Hero Content */
.hero-content {
  z-index: 1;
  max-width: 900px;
  padding: 2rem;
}
.hero-content h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  font-family:poppins;
}
.hero-content p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  font-family: nunito sans;
}
.cta-button {
  background: transparent;
  color: white;
  padding: 0.6rem 1.3rem;
  border-radius: 15px;
  font-weight: bold;
  text-decoration: none;
  transition: background 0.3s ease;
  border: 1px solid white; /* <-- added border */
  margin-left:500px;
}

.cta-button:hover {
  background: white;
  color: rgb(27, 25, 25);
}

/* Background Slideshow Animation */
@keyframes slideshow {
  0% { background-image: url('/public/images/Component 15.png'); }
  33% { background-image: url('/public/images/Component 16.png'); }
  66% { background-image: url('/public/images/Component 17.png'); }
  100% { background-image: url('/public/images/Component 15.png'); }
}

/* ========== Responsive Styles ========== */
@media (max-width: 992px) {
  .hero-content h1 {
    font-size: 2rem;
  }

  .hero-content p {
    font-size: 1rem;
  }

  .cta-button {
    padding: 0.7rem 1.2rem;
    font-size: 0.95rem;
  }

  .floating-element {
    font-size: 2rem;
  }
}

@media (max-width: 576px) {
  .hero {
    padding: 1rem;
    height: 90vh;
    flex-direction: column;
    padding-top: 100px; 
  }

  .hero-content h1 {
    font-size: 1.6rem;
  }

  .hero-content p {
    font-size: 0.95rem;
  }

  .cta-button {
    padding: 0.6rem 1rem;
    font-size: 0.9rem;
    margin-left:10px;
  }

  .floating-element {
    display: none; /* Hide emojis on very small screens */
  }
}
.image-section {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 2rem;
  background-color: #f5f5f5;
}

.image-section img {
  width: 100%;
  max-width: 1300px;
  height: auto;
  
  object-fit: cover;
 
}



/* ========== Responsive Breakpoints ========== */
@media (max-width: 768px) {
  .image-section {
    padding: 0.8rem;
  }

  .image-section img {
    max-width: 90%;
  }
}

@media (max-width: 480px) {
  .image-section {
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
  }

  .image-section img {
    max-width: 100%;
  }
}


.imgsection {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.event-card {
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  height: 250px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.event-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.event-card::before {
  content: '';
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;

  z-index: 1;
}

.past-event-badge {
  position: absolute;
  top: 15px;
  right: 15px;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 500;
  z-index: 2;
}

.event-content {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20px;
  color: white;
  z-index: 2;
  font-family: poppins;
}

.event-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 6px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.event-date {
  font-size: 13px;
  font-weight: 600;
  margin-bottom: 6px;
  opacity: 0.9;
}

.event-description {
  font-size: 13px;
  line-height: 1.4;
  opacity: 0.95;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
}
@media (max-width: 768px) {
  .event-card {
    height: 220px;
  }

  .event-title {
    font-size: 16px;
  }

  .event-content {
    padding: 15px;
  }
}

@media (max-width: 480px) {
  .imgsection {
    padding: 15px 10px;
    gap: 15px;
  }

  .event-card {
    height: 200px;
  }

  .event-title {
    font-size: 15px;
  }

  .event-description {
    font-size: 12px;
  }

  .past-event-badge {
    font-size: 10px;
    padding: 4px 10px;
  }
}
.container {
  max-width: 1200px;
  width: 100%;
  text-align: center;
  padding: 80px 20px;
  margin: 0 auto; 
  scroll-margin-top: 80px;
}

.main-title {
  font-family: clash display;
  font-size: 52px;
  font-weight: 800;
  color: #242565;
  margin-bottom: 30px;
  letter-spacing: -2px;
  text-shadow: 0 4px 8px rgba(74, 71, 163, 0.1);
}

.subtitle {
  font-family: poppins;
  font-size: 18px;
  color: #6a6a6a;
  line-height: 1.6;
  margin-bottom: 80px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  font-weight: 400;
}

.features-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 40px;
  position: relative;
}

.feature-item {
  flex: 1 1 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
}

.icon-circle {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  
}

.icon-circle.calendar {
  background: linear-gradient(135deg, #d9e6fe 0%, #d9e6fe 100%);
}

.icon-circle.offers {
  background: linear-gradient(135deg, #f0e5ff 0%, #f0e5ff 100%);
}

.icon-circle.location {
  background: linear-gradient(135deg, #dcfae3 0%, #dcfae3 100%);
}

.icon {
  width: 40px;
  height: 40px;
  fill: white;
}

.feature-title {
  font-family: poppins;
  font-size: 24px;
  font-weight: 700;
  color: #374151;
  margin-bottom: 15px;
  text-align: center;
}

.feature-content {
  font-family: poppins;
  color: #9ca3af;
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
}

.feature-date {
  font-weight: 600;
  color: #6b7280;
}

.button {
  background: linear-gradient(135deg, #4f46e5 0%, #7c3aed 100%);
  color: white;
  padding: 12px 24px;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  margin-top:40px;
  text-decoration: none;
  display: inline-block;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px rgba(79, 70, 229, 0.3);
}

.button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(79, 70, 229, 0.4);
}

.button:active {
  transform: translateY(0);
  box-shadow: 0 6px 20px rgba(79, 70, 229, 0.3);
}

/* Optional: Center the button */
.button-center {
  text-align: center;
  margin-top: 40px;
}
/* ===== Base Styles ===== */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
