/* ---------------about----------- */
.about-section {
  max-width: 100%;
  margin: 0 auto;
  padding: 2rem;
  background: #ffffff;
}

.container {
  /* display: flex; */
  /* justify-content: space-between; */
  align-items: left;
  flex-wrap: wrap;
  gap: 1rem;
}

/* Left Side Image */
.images1 {
  flex: 1;
  display: flex;
  justify-content: flex-start;
}

.images1 img {
  max-width: 100%;
  height: auto;
}

/* Title + Description */
.about-content-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 2rem;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 2rem;
}

.title {
  font-size: 3rem;
  font-weight: 700;
  color: #000;
  font-family: 'Clash Display', sans-serif;
  flex: 1;
  min-width: 250px;
}

.description {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #333;
  flex: 2;
  max-width: 700px;
  font-family: 'Poppins', sans-serif;
  text-align: left;
}

.highlight, .tech-event {
  color: #6366f1;
  font-weight: 600;
}

/* Right Side Image */
.images2 {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  margin-top: 2rem;
}

.images2 img {
  max-width: 100%;
  height: auto;
}

/* ===== RESPONSIVE ===== */

/* Tablet */
@media (max-width: 1024px) {
  .about-content-wrapper {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .title, .description {
    text-align: center;
    max-width: 100%;
    margin: 0;
  }

  .images2 {
    justify-content: center;
    margin-top: 1rem;
  }
}

/* Mobile */
@media (max-width: 600px) {
  .title {
    font-size: 1.2rem;
    text-align: left;
  }

  .description {
    font-size: 0.7rem;
    text-align: left;
  }

  .images1,
  .images2 {
    justify-content: center;
  }

  .images1 img{
    width: 10%;
    margin-right: 280px;
  }
  .images2 img {
    width: 10%;
    margin-left:280px;
  }
}
